import React, {useState} from 'react';

type CollapsedSelectedType = {
    title:string
}



export const CollapsedSelected = (props:CollapsedSelectedType) => {

    const [editMode,setEdit]=useState(false)

    const ChangeStatus=() => {
        setEdit(!editMode)
    }

    const ChangeEnableSpan =()=> {

    }

    return (
        <> {editMode
            ? <select value={props.title} onBlur={ChangeStatus} autoFocus onChange={ChangeEnableSpan}>
                <option>{'Kiev'}</option>
                <option>{'Minsk'}</option>
        </select>


            : <span onDoubleClick={ChangeStatus}>{props.title}</span>}
        </>
    );
};

